	onLoad(options) {
		let that = this;
		let num = 1;
		that.host = that.$app.globalData.host;
		if (that.$stringUtil.isNotEmpty(options.num)) {
			num = options.num;
		}
		that.num = num;
		// 获取兑换商品信息
		that.getWares(options.waresId);
	},
	onShow() {
